<template>
  <div class="hot">
     <div class="search-hot">
      <div class="search-hot-title">近期热搜</div>
      <div class="search-hot-rm">
        <div class="search-hot-item fl" v-for="(item,index) in hotdata" :key="index" @click="gethotdetaildata(item)">
          <van-icon class="hot-icon" color="#b63232" name="fire" />{{item.first}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        hotdata:[],
      }
    },
    created() {
      this.getHotRm()
    },
    methods: {
      //搜索推荐
      gethotdetaildata(item){
        this.$router.replace({path:'/searchDetail',query:{wkey:item.first}}) 
      },
      getHotRm() {
      this.axios({
        method: "GET",
        url:"/search/hot",
      })
        .then((result) => {
          if (result.data.code == 200) {
            this.hotdata = result.data.result.hots
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },
    
    },
  }
</script>

